<template>
  <view class="card-box">
    <view v-for="(item, index) in showList" :key="index" class="item-box">
      <view class="title ellipsis">{{ item.title }}</view>
      <view class="num ellipsis">
        <text>{{ item.num }}</text>
        <text class="unit" v-if="showUnit(item)">
          {{ item.unit }}
        </text>
      </view>
    </view>
  </view>
</template>

<script>
export default {
  name: 'staticInfoCard',
  props: {
    list: {
      type: Array,
      default: () => {
        return []
      },
    },
  },
  computed: {
    showList() {
      return this.list.filter((item) => {
        return item.show
      })
    },
  },
  data() {
    return {}
  },
  methods: {
    showUnit(item) {
      return item.num > 0
    },
  },
}
</script>

